<template>
  <div id="app">
    <header>
      <h1>To Do List:</h1>
      <hr>
    </header>
    <div id="item-list">
      <span v-if="listToDoItems.length == 0">У вас еще ни одного дела на сегодня!</span>
      <ListItem :id="index" :msg="message" v-for="(message, index) of listToDoItems" :key="index" @remove="removeItem" />
    </div>
    <div id="control-items">
      <input id="inputText" v-on:keyup.enter="addListItem()" type="text" maxlength="64" />
      <div id="add-button" v-on:click="addListItem()">
        <span>Добавить элемент</span>

      </div>

    </div>
  </div>
</template>

<script>
import ListItem from './components/ListItem.vue'

export default {
  name: 'App',
  components: {
    ListItem
  },
  data: function () {
    return {
      listToDoItems: [],
      itemCounter: 0
    }
  },
  methods: {
    addListItem() {
      var val = document.getElementById('inputText').value;
      this.listToDoItems.push(val)
      this.itemCounter += 1
      document.getElementById('inputText').value = null;
    },
    removeItem(data) {
      console.log(data)
      this.listToDoItems.splice(data, 1)
    }
  }
}
</script>

